
import { Observable } from 'rxjs';

/**
* Provides a wrapper for accessing the web storage API and synchronizing session storage across tabs/windows.
*/
export interface ILocalStoreManagerContract {

    initialiseStorageSyncListener();

    deinitialiseStorageSyncListener();

    clearAllStorage();

    clearAllSessionsStorage();

    clearInstanceSessionStorage();

    clearLocalStorage();

    saveSessionData(data: any, key: string);

    saveSyncedSessionData(data: any, key: string);

    savePermanentData(data: any, key: string);

    moveDataToSessionStorage(key: string);

    moveDataToSyncedSessionStorage(key: string);

    moveDataToPermanentStorage(key: string);

    exists(key: string);

    getData(key: string);

    getDataObject<T>(key: string, isDateType: boolean);

    deleteData(key: string);

    getInitEvent(): Observable<{}>;
}

export class StorageManagerConstants {
    public static readonly DBKEY_USER_DATA = 'user_data';
}    
